import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import {useStaticQuery, graphql} from "gatsby"
import Img from "gatsby-image"
import foto1 from "../images/convert-audio-mac.png"
import PostIndex2 from "../components/postIndex2/postIndex2";


const Mac = () => {
    return (
        <Layout sideNavigation={true}>
            <SEO
                title="Vitamina B7"
                meta="Toda la información que necesitas sobre la vitamina B7. ¿Qué es? ¿Qué funciones hace? ¿Síntomas de déficit de vitamina B7?"
                description="Toda la información que necesitas sobre la vitamina B7. ¿Qué es? ¿Qué funciones hace? ¿Síntomas de déficit de vitamina B7?"
                lang="es"
            />
            <h1>Vitamina B7</h1>
            <PostIndex2 linksData={[
                {"url": "#necesidades", "text": "Cantidad"},
                {"url": "#deficiencia", "text": "Deficiencia"},
                {"url": "#alimentos", "text": "Alimentos"},
                {"url": "#suplementos", "text": "Suplementos"},
            ]}
            />
            <hr />
            <p class="introductionText">La vitamina B7 pertenece al grupo de las <a className="link" href="/vitaminaB">vitaminas B</a>. Tambi&eacute;n se conoce como biotina. El cuerpo
                necesita peque&ntilde;as cantidades de biotina para metabolizar los carbohidratos, las grasas y las
                prote&iacute;nas.</p>
            <hr />
            <p></p>
            <p>Las bacterias que viven naturalmente en el intestino producen biotina, por lo que no est&aacute; claro si
                necesitamos biotina adicional en nuestra dieta.</p>
            <p></p>
            <p>La vitamina B7 se recomienda a menudo para fortalecer el cabello y las u&ntilde;as y se encuentra en
                muchos productos cosm&eacute;ticos para el cabello y la piel. Sin embargo, hay pocas pruebas que
                demuestren alg&uacute;n beneficio.</p>
            <p></p>

            <h2 name={"necesidades"}>Necesidades diarias de vitamina B7</h2>
            <p>No hay una dosis diaria recomendada (RDA) de vitamina B7 en España porque no hay suficientes
                pruebas para establecer una. Una cantidad sugerida que es segura y adecuada es 20&micro;g (microgramos)
                por d&iacute;a.</p>
            <p></p>
            <h2 name={"deficiencia"}>Deficiencia de vitamina B7</h2>
            <p>&iquest;Qu&eacute; pasa si no tengo suficiente vitamina B7? La deficiencia de vitamina B7 es
                extremadamente rara. Esto se debe a que la vitamina B es producida por bacterias en el intestino y
                est&aacute; ampliamente disponible en los alimentos.</p>
            <p></p>
            <p>La deficiencia de vitamina B7 puede ser causada por comer claras de huevo crudas (dos o m&aacute;s al
                d&iacute;a) durante muchos meses, ya que la clara de huevo cruda contiene una prote&iacute;na llamada
                avidina, que se une a la vitamina B7.</p>
            <p></p>
            <p>Las personas con trastornos gen&eacute;ticos, como la deficiencia de biotinidasa, la deficiencia de
                carboxilasa m&uacute;ltiple y la deficiencia de holocarboxilasa sintetasa tambi&eacute;n son
                susceptibles a la deficiencia de vitamina B7.</p>
            <p></p>
            <h3>Los s&iacute;ntomas de deficiencia de vitamina B7 incluyen:</h3>
            <p></p>
           <ul>
               <li>una dermatitis seborreica fina y escamosa y/o un sarpullido rojo, generalmente en la cara</li>
               <li>cabello quebradizo o p&eacute;rdida de cabello</li>
               <li>Conjuntivitis</li>
               <li>mala coordinaci&oacute;n de los movimientos corporales (ataxia)</li>
               <li>letargo</li>
               <li>p&eacute;rdida de apetito y n&aacute;useas</li>
               <li>depresi&oacute;n leve</li>
               <li>alucinaci&oacute;n</li>
               <li>defectos de nacimiento (notificados en animales)</li>
               <li>posible inmunidad reducida y susceptibilidad a infecciones bacterianas/f&uacute;ngicas</li>
           </ul>
            <h2 name={"alimentos"}>Alimentos con vitamina B7</h2>
            <p></p>
            <ul>
                <li>h&iacute;gado</li>
                <li>yema de huevo</li>
                <li>semillas y frutos secos, por ejemplo, almendras</li>
                <li>verduras de hoja verde como espinacas y acelgas</li>
                <li>las patatas dulces...</li>
                <li>bayas de saskatoon</li>
            </ul>
            <p>&iquest;Cu&aacute;les son los efectos de la cocci&oacute;n en la vitamina B7? La vitamina B7 no es
                particularmente susceptible de perderse durante la cocci&oacute;n.</p>
            <p></p>
            <p>Cuando se cocina, es importante utilizar alimentos frescos, cocer al vapor en lugar de hervir y evitar
                tiempos de cocci&oacute;n demasiado largos para preservar las vitaminas.</p>
            <p></p>
            <h2 name={"suplementos"}>&iquest;Deber&iacute;a tomar un suplemento de vitamina B7?</h2>
            <p>El Departamento de Salud aconseja que debes obtener toda la vitamina B7 que necesites comiendo una dieta
                sana y equilibrada.</p>
            <p></p>
            <p>Las personas que se someten a di&aacute;lisis renal o reciben toda su nutrici&oacute;n a trav&eacute;s de
                un goteo, pueden necesitar un suplemento de vitamina B7, pero esto debe ser comprobado con el proveedor
                de atenci&oacute;n m&eacute;dica correspondiente.</p>
            <p></p>
            <p>Las investigaciones sugieren que los suplementos de vitamina B7 pueden desempe&ntilde;ar un papel en la
                mejora de los niveles de az&uacute;car en la sangre en las personas con diabetes, pero es necesario
                realizar m&aacute;s investigaciones para confirmarlo.</p>
            <p></p>
            <p>Se puede aconsejar a las mujeres embarazadas con bajos niveles de vitamina B7 que tomen suplementos de
                vitamina B7.</p>
            <p></p>
            <p>Las pruebas preliminares sugieren que la vitamina B7 podr&iacute;a ayudar a fortalecer las u&ntilde;as
                quebradizas, pero el beneficio no est&aacute; demostrado.</p>
            <p></p>
            <p>No se han probado otros usos de los suplementos de vitamina B7 para afecciones como la costra
                l&aacute;ctea, la p&eacute;rdida de cabello, la depresi&oacute;n y la hepatitis.</p>
            <p></p>
            <p>Sin embargo, si usted toma suplementos de biotina, no tome demasiada cantidad ya que esto puede ser
                da&ntilde;ino.</p>
            <p></p>

            <h2>Efectos secundarios de los suplementos de vitamina B7</h2>
            <p>&iquest;Cu&aacute;les son los efectos secundarios y las precauciones de seguridad de tomar un suplemento
                de vitamina B7? La deficiencia es poco frecuente y la suplementaci&oacute;n no suele ser necesaria.</p>
            <p></p>
            <p>Se considera que la vitamina B7 es segura para la mayor&iacute;a de las personas cuando se toma por
                v&iacute;a oral en la dosis correcta. No se cree que haya ning&uacute;n da&ntilde;o en tomar hasta 0.9mg
                diarios.</p>
            <p></p>
            <h3>&iquest;Qu&eacute; sucede si tomo demasiada vitamina B7?</h3>
            <p></p>
            <p>La vitamina B7 es una vitamina soluble en agua, lo que significa que cualquier exceso se excreta en la
                orina y las heces, lo que hace que la toxicidad sea poco frecuente.</p>
            <p></p>
            <p>Hay poca informaci&oacute;n disponible sobre la toxicidad, pero si le preocupa que pueda haber tomado una
                sobredosis de vitamina B7, busque asesoramiento m&eacute;dico.</p>
            <p></p>
            <p>Cuando se ingiere un exceso de vitamina B7 junto con la vitamina B5, puede producirse una afecci&oacute;n
                denominada derrame pleuroperic&aacute;rdico eosinof&iacute;lico.</p>
            <p></p>
            <h3>&iquest;Tienen los suplementos de vitamina B7 alguna interacci&oacute;n con los medicamentos?</h3>
            <p>El alcohol y algunos f&aacute;rmacos anticonvulsivos pueden afectar al transporte de la biotina en el
                cuerpo.</p>
        </Layout>
    )
}

export default Mac
