import {Link} from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import "./postIndex2.css"


const PostIndex2 = ({linksData, selectedItem}) => (
    <div>
        <ul className="css-17ztj4b">
            {linksData.map((item, key) => {
                return (
                    <li className="css-uksoej"><a href={item.url} className="css-143q8sx">{item.text}</a></li>
                )
            })}
        </ul>
    </div>
)

export default PostIndex2